import React from "react";

const Navbar = (props) => {
  return (
    <div className="navbar">
      <h1>Where in the world?</h1>
      <div
        className="toggle"
        onClick={() => props.toggle((prevMode) => !prevMode)}
      >
        {props.darkMode ? "‚òÄÔ∏è Light mode" : "üåô Dark mode"}
      </div>
    </div>
  );
};

export default Navbar;
